"use client";

import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Room } from "@/types/room";
import { useState } from "react";

interface RoomDetailsModalProps {
  room: Room | null;
  isOpen: boolean;
  onClose: () => void;
  onEdit?: (room: Room) => void;
  onDelete?: (id: string) => void;
}

export function RoomDetailsModal({ room, isOpen, onClose, onEdit, onDelete }: RoomDetailsModalProps) {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  if (!room) return null;

  const formatCep = (cep: string) => {
    return cep.replace(/(\d{5})(\d{3})/, '$1-$2');
  };

  const getRoomTypeLabel = (type: string) => {
    const labels = {
      'single': 'Single (Solteiro)',
      'double': 'Double (Casal)',
      'double for 1': 'Double for 1 (Casal para 1)',
      'double/single': 'Double/Single (Ambos)'
    };
    return labels[type as keyof typeof labels] || type;
  };

  const nextImage = () => {
    if (room.imagens && room.imagens.length > 1) {
      setCurrentImageIndex((prev) => (prev + 1) % room.imagens.length);
    }
  };

  const prevImage = () => {
    if (room.imagens && room.imagens.length > 1) {
      setCurrentImageIndex((prev) => (prev - 1 + room.imagens.length) % room.imagens.length);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center justify-between">
            <span>Quarto {room.numeroQuarto}</span>
            <Badge variant="outline">
              {getRoomTypeLabel(room.tipoQuarto)}
            </Badge>
          </DialogTitle>
        </DialogHeader>

        <div className="space-y-6">
          {/* Image Gallery */}
          {room.imagens && room.imagens.length > 0 && (
            <div className="space-y-4">
              <div className="relative">
                <img
                  src={room.imagens[currentImageIndex]}
                  alt={`Quarto ${room.numeroQuarto} - Imagem ${currentImageIndex + 1}`}
                  className="w-full h-64 md:h-80 object-cover rounded-lg"
                />
                
                {room.imagens.length > 1 && (
                  <>
                    <Button
                      variant="outline"
                      size="sm"
                      className="absolute left-2 top-1/2 transform -translate-y-1/2 bg-white/90"
                      onClick={prevImage}
                    >
                      ‚Üê
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      className="absolute right-2 top-1/2 transform -translate-y-1/2 bg-white/90"
                      onClick={nextImage}
                    >
                      ‚Üí
                    </Button>
                    <div className="absolute bottom-2 left-1/2 transform -translate-x-1/2 bg-black/50 text-white px-2 py-1 rounded text-sm">
                      {currentImageIndex + 1} / {room.imagens.length}
                    </div>
                  </>
                )}
              </div>

              {room.imagens.length > 1 && (
                <div className="flex gap-2 overflow-x-auto pb-2">
                  {room.imagens.map((image, index) => (
                    <button
                      key={index}
                      onClick={() => setCurrentImageIndex(index)}
                      className={`flex-shrink-0 w-16 h-16 rounded border-2 overflow-hidden ${
                        index === currentImageIndex ? 'border-primary' : 'border-muted'
                      }`}
                    >
                      <img
                        src={image}
                        alt={`Thumbnail ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>
          )}

          {/* Room Details */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <div>
                <h3 className="font-semibold text-lg mb-2">Informa√ß√µes B√°sicas</h3>
                <div className="space-y-2">
                  <div>
                    <span className="text-sm text-muted-foreground">N√∫mero do Quarto:</span>
                    <p className="font-medium">{room.numeroQuarto}</p>
                  </div>
                  <div>
                    <span className="text-sm text-muted-foreground">Tipo:</span>
                    <p className="font-medium">{getRoomTypeLabel(room.tipoQuarto)}</p>
                  </div>
                  <div>
                    <span className="text-sm text-muted-foreground">CEP:</span>
                    <p className="font-medium">{formatCep(room.cep)}</p>
                  </div>
                </div>
              </div>

              <div>
                <h3 className="font-semibold text-lg mb-2">Endere√ßo</h3>
                <p className="text-sm">{room.endereco}</p>
              </div>
            </div>

            <div className="space-y-4">
              <div>
                <h3 className="font-semibold text-lg mb-2">Valores</h3>
                <div className="space-y-2">
                  <div className="flex justify-between items-center p-3 bg-muted rounded-lg">
                    <span className="text-sm text-muted-foreground">Valor em Libras:</span>
                    <span className="font-semibold text-lg">¬£{room.valorLibras}</span>
                  </div>
                  <div className="flex justify-between items-center p-3 bg-green-50 dark:bg-green-900/20 rounded-lg">
                    <span className="text-sm text-muted-foreground">Valor Convertido:</span>
                    <span className="font-semibold text-lg text-green-600">
                      R$ {room.valorConvertido.toFixed(2)}
                    </span>
                  </div>
                </div>
              </div>

              <div>
                <h3 className="font-semibold text-lg mb-2">Lugares Pr√≥ximos</h3>
                <p className="text-sm whitespace-pre-wrap">{room.lugaresProximos}</p>
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="flex gap-2 pt-4 border-t">
            {onEdit && (
              <Button
                onClick={() => {
                  onEdit(room);
                  onClose();
                }}
                className="flex-1"
              >
                Editar Quarto
              </Button>
            )}
            {onDelete && room._id && (
              <Button
                variant="destructive"
                onClick={() => {
                  if (confirm('Tem certeza que deseja excluir este quarto?')) {
                    onDelete(room._id!);
                    onClose();
                  }
                }}
                className="flex-1"
              >
                Excluir Quarto
              </Button>
            )}
            <Button variant="outline" onClick={onClose} className="flex-1">
              Fechar
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
