"use client";

import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { SearchFilters } from "@/types/room";

interface SearchBarProps {
  onSearch: (filters: SearchFilters) => void;
  onClear: () => void;
  isLoading?: boolean;
}

export function SearchBar({ onSearch, onClear, isLoading = false }: SearchBarProps) {
  const [filters, setFilters] = useState<SearchFilters>({
    cep: '',
    tipoQuarto: '',
    numeroQuarto: '',
  });

  const handleInputChange = (field: keyof SearchFilters, value: string) => {
    // Handle "all" value for room type
    if (field === 'tipoQuarto' && value === 'all') {
      value = '';
    }
    const newFilters = { ...filters, [field]: value };
    setFilters(newFilters);
  };

  const handleSearch = () => {
    // Remove empty filters
    const activeFilters: SearchFilters = {};
    if (filters.cep?.trim()) activeFilters.cep = filters.cep.trim();
    if (filters.tipoQuarto?.trim()) activeFilters.tipoQuarto = filters.tipoQuarto.trim();
    if (filters.numeroQuarto?.trim()) activeFilters.numeroQuarto = filters.numeroQuarto.trim();
    
    onSearch(activeFilters);
  };

  const handleClear = () => {
    setFilters({
      cep: '',
      tipoQuarto: '',
      numeroQuarto: '',
    });
    onClear();
  };

  const hasActiveFilters = filters.cep || filters.tipoQuarto || filters.numeroQuarto;

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg">Buscar Quartos</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="space-y-2">
            <Label htmlFor="search-cep">CEP</Label>
            <Input
              id="search-cep"
              placeholder="00000-000"
              value={filters.cep}
              onChange={(e) => handleInputChange('cep', e.target.value)}
              disabled={isLoading}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="search-tipo">Tipo de Quarto</Label>
            <Select
              value={filters.tipoQuarto}
              onValueChange={(value) => handleInputChange('tipoQuarto', value)}
              disabled={isLoading}
            >
              <SelectTrigger>
                <SelectValue placeholder="Todos os tipos" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Todos os tipos</SelectItem>
                <SelectItem value="single">Single (Solteiro)</SelectItem>
                <SelectItem value="double">Double (Casal)</SelectItem>
                <SelectItem value="double for 1">Double for 1 (Casal para 1)</SelectItem>
                <SelectItem value="double/single">Double/Single (Ambos)</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label htmlFor="search-numero">N√∫mero do Quarto</Label>
            <Input
              id="search-numero"
              placeholder="Ex: 101, A1"
              value={filters.numeroQuarto}
              onChange={(e) => handleInputChange('numeroQuarto', e.target.value)}
              disabled={isLoading}
            />
          </div>
        </div>

        <div className="flex gap-2 mt-4">
          <Button
            onClick={handleSearch}
            disabled={isLoading}
            className="flex-1 md:flex-none"
          >
            {isLoading ? 'Buscando...' : 'Buscar'}
          </Button>
          
          {hasActiveFilters && (
            <Button
              variant="outline"
              onClick={handleClear}
              disabled={isLoading}
              className="flex-1 md:flex-none"
            >
              Limpar Filtros
            </Button>
          )}
        </div>

        {hasActiveFilters && (
          <div className="mt-3 text-sm text-muted-foreground">
            Filtros ativos: {Object.entries(filters).filter(([_, value]) => value).length}
          </div>
        )}
      </CardContent>
    </Card>
  );
}
