"use client";

import { useState, useRef } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

interface ImageUploadProps {
  onImagesChange: (images: string[]) => void;
  existingImages?: string[];
}

export function ImageUpload({ onImagesChange, existingImages = [] }: ImageUploadProps) {
  const [images, setImages] = useState<string[]>(existingImages);
  const [uploading, setUploading] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileSelect = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files;
    if (!files || files.length === 0) return;

    setUploading(true);

    try {
      const formData = new FormData();
      Array.from(files).forEach(file => {
        formData.append('files', file);
      });

      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      });

      const result = await response.json();

      if (response.ok) {
        const newImages = [...images, ...result.files];
        setImages(newImages);
        onImagesChange(newImages);
      } else {
        alert(result.error || 'Erro ao fazer upload das imagens');
      }
    } catch (error) {
      console.error('Erro no upload:', error);
      alert('Erro ao fazer upload das imagens');
    } finally {
      setUploading(false);
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  };

  const removeImage = (index: number) => {
    const newImages = images.filter((_, i) => i !== index);
    setImages(newImages);
    onImagesChange(newImages);
  };

  return (
    <div className="space-y-4">
      <div>
        <Label htmlFor="images">Imagens do Quarto *</Label>
        <div className="mt-2">
          <Input
            ref={fileInputRef}
            id="images"
            type="file"
            multiple
            accept="image/*"
            onChange={handleFileSelect}
            disabled={uploading}
            className="cursor-pointer"
          />
          <p className="text-sm text-muted-foreground mt-1">
            Selecione uma ou mais imagens (JPG, PNG, WebP)
          </p>
        </div>
      </div>

      {uploading && (
        <div className="text-sm text-muted-foreground">
          Fazendo upload das imagens...
        </div>
      )}

      {images.length > 0 && (
        <div className="space-y-2">
          <Label>Imagens Selecionadas ({images.length})</Label>
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {images.map((image, index) => (
              <div key={index} className="relative group">
                <img
                  src={image}
                  alt={`Imagem ${index + 1}`}
                  className="w-full h-24 object-cover rounded-md border"
                />
                <Button
                  type="button"
                  variant="destructive"
                  size="sm"
                  className="absolute top-1 right-1 opacity-0 group-hover:opacity-100 transition-opacity h-6 w-6 p-0"
                  onClick={() => removeImage(index)}
                >
                  √ó
                </Button>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}
