"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Toaster } from "@/components/ui/sonner";
import { toast } from "sonner";
import { ThemeToggle } from "@/components/ThemeToggle";
import { RoomForm } from "@/components/RoomForm";
import { SearchBar } from "@/components/SearchBar";
import { RoomGrid } from "@/components/RoomGrid";
import { RoomDetailsModal } from "@/components/RoomDetailsModal";
import { Room, SearchFilters } from "@/types/room";

export default function HomePage() {
  const [rooms, setRooms] = useState<Room[]>([]);
  const [filteredRooms, setFilteredRooms] = useState<Room[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [editingRoom, setEditingRoom] = useState<Room | null>(null);
  const [selectedRoom, setSelectedRoom] = useState<Room | null>(null);
  const [isDetailsOpen, setIsDetailsOpen] = useState(false);

  // Fetch rooms from API
  const fetchRooms = async (filters?: SearchFilters) => {
    setIsLoading(true);
    try {
      const queryParams = new URLSearchParams();
      if (filters?.cep) queryParams.append('cep', filters.cep);
      if (filters?.tipoQuarto) queryParams.append('tipoQuarto', filters.tipoQuarto);
      if (filters?.numeroQuarto) queryParams.append('numeroQuarto', filters.numeroQuarto);

      const response = await fetch(`/api/quartos?${queryParams.toString()}`);
      const result = await response.json();

      if (result.success) {
        setRooms(result.data);
        setFilteredRooms(result.data);
      } else {
        toast.error('Erro ao carregar quartos');
      }
    } catch (error) {
      console.error('Erro ao buscar quartos:', error);
      toast.error('Erro ao carregar quartos');
    } finally {
      setIsLoading(false);
    }
  };

  // Load rooms on component mount
  useEffect(() => {
    fetchRooms();
  }, []);

  // Handle room creation/update
  const handleRoomSubmit = async (roomData: Room) => {
    setIsLoading(true);
    try {
      const url = editingRoom ? `/api/quartos/${editingRoom._id}` : '/api/quartos';
      const method = editingRoom ? 'PUT' : 'POST';

      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(roomData),
      });

      const result = await response.json();

      if (result.success) {
        toast.success(result.message || (editingRoom ? 'Quarto atualizado!' : 'Quarto cadastrado!'));
        setIsFormOpen(false);
        setEditingRoom(null);
        fetchRooms(); // Refresh the list
      } else {
        toast.error(result.error || 'Erro ao salvar quarto');
      }
    } catch (error) {
      console.error('Erro ao salvar quarto:', error);
      toast.error('Erro ao salvar quarto');
    } finally {
      setIsLoading(false);
    }
  };

  // Handle room deletion
  const handleRoomDelete = async (id: string) => {
    if (!confirm('Tem certeza que deseja excluir este quarto?')) return;

    setIsLoading(true);
    try {
      const response = await fetch(`/api/quartos/${id}`, {
        method: 'DELETE',
      });

      const result = await response.json();

      if (result.success) {
        toast.success('Quarto exclu√≠do com sucesso!');
        fetchRooms(); // Refresh the list
      } else {
        toast.error(result.error || 'Erro ao excluir quarto');
      }
    } catch (error) {
      console.error('Erro ao excluir quarto:', error);
      toast.error('Erro ao excluir quarto');
    } finally {
      setIsLoading(false);
    }
  };

  // Handle search
  const handleSearch = (filters: SearchFilters) => {
    fetchRooms(filters);
  };

  // Handle clear search
  const handleClearSearch = () => {
    fetchRooms();
  };

  // Handle edit room
  const handleEditRoom = (room: Room) => {
    setEditingRoom(room);
    setIsFormOpen(true);
  };

  // Handle view room details
  const handleViewRoom = (room: Room) => {
    setSelectedRoom(room);
    setIsDetailsOpen(true);
  };

  // Handle new room
  const handleNewRoom = () => {
    setEditingRoom(null);
    setIsFormOpen(true);
  };

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-8">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">
              Sistema de Gest√£o de Quartos
            </h1>
            <p className="text-muted-foreground mt-1">
              Gerencie seus quartos de aluguel de forma eficiente
            </p>
          </div>
          <div className="flex items-center gap-2">
            <ThemeToggle />
            <Dialog open={isFormOpen} onOpenChange={setIsFormOpen}>
              <DialogTrigger asChild>
                <Button onClick={handleNewRoom}>
                  Cadastrar Quarto
                </Button>
              </DialogTrigger>
              <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
                <DialogHeader>
                  <DialogTitle>
                    {editingRoom ? 'Editar Quarto' : 'Cadastrar Novo Quarto'}
                  </DialogTitle>
                </DialogHeader>
                <RoomForm
                  onSubmit={handleRoomSubmit}
                  initialData={editingRoom || undefined}
                  isLoading={isLoading}
                />
              </DialogContent>
            </Dialog>
          </div>
        </div>

        {/* Search Bar */}
        <div className="mb-8">
          <SearchBar
            onSearch={handleSearch}
            onClear={handleClearSearch}
            isLoading={isLoading}
          />
        </div>

        {/* Stats */}
        <div className="mb-6">
          <div className="flex flex-wrap gap-4 text-sm text-muted-foreground">
            <span>Total de quartos: {rooms.length}</span>
            {filteredRooms.length !== rooms.length && (
              <span>Resultados filtrados: {filteredRooms.length}</span>
            )}
          </div>
        </div>

        {/* Room Grid */}
        <RoomGrid
          rooms={filteredRooms}
          onEdit={handleEditRoom}
          onDelete={handleRoomDelete}
          onView={handleViewRoom}
          isLoading={isLoading}
        />

        {/* Room Details Modal */}
        <RoomDetailsModal
          room={selectedRoom}
          isOpen={isDetailsOpen}
          onClose={() => setIsDetailsOpen(false)}
          onEdit={handleEditRoom}
          onDelete={handleRoomDelete}
        />

        {/* Toast Notifications */}
        <Toaster />
      </div>
    </div>
  );
}
