"use client";

import { Card, CardContent, CardFooter, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Room } from "@/types/room";

interface RoomCardProps {
  room: Room;
  onEdit?: (room: Room) => void;
  onDelete?: (id: string) => void;
  onView?: (room: Room) => void;
}

export function RoomCard({ room, onEdit, onDelete, onView }: RoomCardProps) {
  const formatCep = (cep: string) => {
    return cep.replace(/(\d{5})(\d{3})/, '$1-$2');
  };

  const getRoomTypeLabel = (type: string) => {
    const labels = {
      'single': 'Solteiro',
      'double': 'Casal',
      'double for 1': 'Casal p/ 1',
      'double/single': 'Ambos'
    };
    return labels[type as keyof typeof labels] || type;
  };

  const mainImage = room.imagens && room.imagens.length > 0 ? room.imagens[0] : null;

  return (
    <Card className="h-full flex flex-col hover:shadow-lg transition-shadow">
      <CardHeader className="p-0">
        <div className="relative h-48 bg-muted rounded-t-lg overflow-hidden">
          {mainImage ? (
            <img
              src={mainImage}
              alt={`Quarto ${room.numeroQuarto}`}
              className="w-full h-full object-cover"
            />
          ) : (
            <div className="w-full h-full flex items-center justify-center bg-muted">
              <span className="text-muted-foreground text-sm">Sem imagem</span>
            </div>
          )}
          <div className="absolute top-2 left-2">
            <Badge variant="secondary" className="bg-white/90 text-black">
              Quarto {room.numeroQuarto}
            </Badge>
          </div>
          <div className="absolute top-2 right-2">
            <Badge variant="outline" className="bg-white/90">
              {getRoomTypeLabel(room.tipoQuarto)}
            </Badge>
          </div>
        </div>
      </CardHeader>

      <CardContent className="flex-1 p-4">
        <div className="space-y-3">
          <div>
            <p className="text-sm text-muted-foreground">CEP</p>
            <p className="font-medium">{formatCep(room.cep)}</p>
          </div>

          <div>
            <p className="text-sm text-muted-foreground">Endere√ßo</p>
            <p className="text-sm line-clamp-2">{room.endereco}</p>
          </div>

          <div className="grid grid-cols-2 gap-2">
            <div>
              <p className="text-sm text-muted-foreground">Valor</p>
              <p className="font-semibold text-lg">¬£{room.valorLibras}</p>
            </div>
            <div>
              <p className="text-sm text-muted-foreground">Convertido</p>
              <p className="font-semibold text-lg text-green-600">
                R$ {room.valorConvertido.toFixed(2)}
              </p>
            </div>
          </div>

          <div>
            <p className="text-sm text-muted-foreground">Lugares Pr√≥ximos</p>
            <p className="text-sm line-clamp-2">{room.lugaresProximos}</p>
          </div>

          {room.imagens && room.imagens.length > 1 && (
            <div>
              <p className="text-sm text-muted-foreground">
                +{room.imagens.length - 1} imagem{room.imagens.length > 2 ? 's' : ''}
              </p>
            </div>
          )}
        </div>
      </CardContent>

      <CardFooter className="p-4 pt-0 space-x-2">
        {onView && (
          <Button
            variant="outline"
            size="sm"
            onClick={() => onView(room)}
            className="flex-1"
          >
            Ver Detalhes
          </Button>
        )}
        {onEdit && (
          <Button
            variant="outline"
            size="sm"
            onClick={() => onEdit(room)}
            className="flex-1"
          >
            Editar
          </Button>
        )}
        {onDelete && room._id && (
          <Button
            variant="destructive"
            size="sm"
            onClick={() => onDelete(room._id!)}
            className="flex-1"
          >
            Excluir
          </Button>
        )}
      </CardFooter>
    </Card>
  );
}
