"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { ImageUpload } from "./ImageUpload";
import { roomSchema, type RoomFormData } from "@/lib/validations";
import { Room } from "@/types/room";

interface RoomFormProps {
  onSubmit: (data: Room) => void;
  initialData?: Room;
  isLoading?: boolean;
}

type FormData = {
  endereco: string;
  cep: string;
  tipoQuarto: 'single' | 'double' | 'double for 1' | 'double/single';
  valorLibras: number;
  numeroQuarto: string;
  lugaresProximos: string;
};

export function RoomForm({ onSubmit, initialData, isLoading = false }: RoomFormProps) {
  const [images, setImages] = useState<string[]>(initialData?.imagens || []);
  const [valorConvertido, setValorConvertido] = useState<number>(initialData?.valorConvertido || 0);

  const {
    register,
    handleSubmit,
    setValue,
    watch,
    formState: { errors },
  } = useForm<FormData>({
    defaultValues: initialData ? {
      endereco: initialData.endereco,
      cep: initialData.cep,
      tipoQuarto: initialData.tipoQuarto,
      valorLibras: initialData.valorLibras,
      numeroQuarto: initialData.numeroQuarto,
      lugaresProximos: initialData.lugaresProximos,
    } : undefined,
  });

  const valorLibras = watch("valorLibras");

  // Calculate converted value when pound value changes
  const handleValorLibrasChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseFloat(e.target.value) || 0;
    const rate = parseFloat(process.env.NEXT_PUBLIC_CURRENCY_RATE || '4.33');
    const converted = value * rate;
    setValorConvertido(converted);
  };

  const handleFormSubmit = (data: FormData) => {
    if (images.length === 0) {
      alert('Pelo menos uma imagem √© obrigat√≥ria');
      return;
    }

    const rate = parseFloat(process.env.NEXT_PUBLIC_CURRENCY_RATE || '4.33');
    const roomData: Room = {
      ...data,
      valorConvertido: data.valorLibras * rate,
      imagens: images,
    };

    onSubmit(roomData);
  };

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <CardHeader>
        <CardTitle>
          {initialData ? 'Editar Quarto' : 'Cadastrar Novo Quarto'}
        </CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(handleFormSubmit)} className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="endereco">Endere√ßo *</Label>
              <Input
                id="endereco"
                {...register("endereco")}
                placeholder="Rua, n√∫mero, bairro"
              />
              {errors.endereco && (
                <p className="text-sm text-red-500">{errors.endereco.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="cep">CEP *</Label>
              <Input
                id="cep"
                {...register("cep")}
                placeholder="00000-000"
                maxLength={9}
              />
              {errors.cep && (
                <p className="text-sm text-red-500">{errors.cep.message}</p>
              )}
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="tipoQuarto">Tipo de Quarto *</Label>
              <Select onValueChange={(value) => setValue("tipoQuarto", value as any)}>
                <SelectTrigger>
                  <SelectValue placeholder="Selecione o tipo" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="single">Single (Solteiro)</SelectItem>
                  <SelectItem value="double">Double (Casal)</SelectItem>
                  <SelectItem value="double for 1">Double for 1 (Casal para 1)</SelectItem>
                  <SelectItem value="double/single">Double/Single (Ambos)</SelectItem>
                </SelectContent>
              </Select>
              {errors.tipoQuarto && (
                <p className="text-sm text-red-500">{errors.tipoQuarto.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="numeroQuarto">N√∫mero do Quarto *</Label>
              <Input
                id="numeroQuarto"
                {...register("numeroQuarto")}
                placeholder="Ex: 101, A1, etc."
              />
              {errors.numeroQuarto && (
                <p className="text-sm text-red-500">{errors.numeroQuarto.message}</p>
              )}
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="valorLibras">Valor em Libras (¬£) *</Label>
              <Input
                id="valorLibras"
                type="number"
                step="0.01"
                min="0"
                {...register("valorLibras", { valueAsNumber: true })}
                onChange={handleValorLibrasChange}
                placeholder="0.00"
              />
              {errors.valorLibras && (
                <p className="text-sm text-red-500">{errors.valorLibras.message}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label>Valor Convertido (R$)</Label>
              <div className="p-3 bg-muted rounded-md">
                <span className="text-lg font-semibold">
                  R$ {valorConvertido.toFixed(2)}
                </span>
                <p className="text-xs text-muted-foreground">
                  Taxa: ¬£1 = R$ {process.env.NEXT_PUBLIC_CURRENCY_RATE || '4.33'}
                </p>
              </div>
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="lugaresProximos">Lugares Pr√≥ximos *</Label>
            <Textarea
              id="lugaresProximos"
              {...register("lugaresProximos")}
              placeholder="Descreva pontos de interesse, transporte p√∫blico, etc."
              rows={3}
            />
            {errors.lugaresProximos && (
              <p className="text-sm text-red-500">{errors.lugaresProximos.message}</p>
            )}
          </div>

          <ImageUpload
            onImagesChange={setImages}
            existingImages={initialData?.imagens}
          />

          <Button type="submit" className="w-full" disabled={isLoading}>
            {isLoading ? 'Salvando...' : (initialData ? 'Atualizar Quarto' : 'Cadastrar Quarto')}
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}
